<template>
  <briup-fulllayout title="">
    <!-- {{addresses}} -->
    <!-- {{info}} -->
    <van-list>
      <van-cell v-for="item in addresses" 
        :key="item.id"
        :title="item.province+' '+item.city+' '+item.area+' '+item.address"/>
    </van-list>
    <br>
    <van-button block type="default" @click="toAddressEditHandler"></van-button>
  </briup-fulllayout>
</template>

<script>
import {get} from '../../../http/axios'
import {mapState} from 'vuex'
export default {
  data(){
    return {
      addresses:[]
    }
  },
  computed:{  // 
    // userinfo
    ...mapState("user",["info"])
  },
  created(){
    // 
    this.loadAddress();
  },
  methods:{
    // 
    loadAddress(){
      let id = this.info.id;  // id26
      let url = "/address/findByCustomerId?id="+id;
      get(url).then((response)=>{
        this.addresses = response.data;
      })
    },
    // 
    toAddressEditHandler(){
      // 
      this.$router.push("/manager/address_edit");
    }
  }
}
</script>