<template>
  <briup-fulllayout title="">
    <div>
      <van-cell-group>
        <van-field v-model="form.telephone" placeholder="" />
      </van-cell-group>
      <van-cell-group>
        <van-field v-model="form.province" placeholder="" />
      </van-cell-group>
       <van-cell-group>
        <van-field v-model="form.city" placeholder="" />
      </van-cell-group>
       <van-cell-group>
        <van-field v-model="form.area" placeholder="" />
      </van-cell-group>
      <van-cell-group>
        <van-field v-model="form.address" placeholder="" />
      </van-cell-group>

      <van-button block type="primary" @click="submitHandler"></van-button>

    </div>
  </briup-fulllayout>
</template>
<script>
import {post} from '../../../http/axios'
import { mapState } from 'vuex';
import { Toast } from 'vant';
export default {
  data(){
    return {
      form:{}
    }
  },
  computed:{
    ...mapState('user',['info'])
  },
  methods:{
    submitHandler(){
      let url = "/address/saveOrUpdate"
      // idid
      this.form.customerId = this.info.id;
      post(url,this.form).then((response)=>{
        // 
        this.$router.go(-1);
        // 
        this.$toast.success(response.message)
      })
    }
  }
}
</script>