<template>
  <briup-fulllayout title="">
    :
    <van-dropdown-menu>
      <van-dropdown-item v-model="addressId" :options="options" />
    </van-dropdown-menu>
    :
    <div style="padding:0 2em">
      <p>{{$route.query.name}}</p>
      <p>{{$route.query.description}}</p>
      <p>{{$route.query.price}}</p>
      <p>1</p>
      <p>{{$route.query.price * 1}}</p>
    </div>
    <div style="position:fixed;bottom:0;width:100%;">
      <van-goods-action>
  <van-goods-action-icon icon="chat-o" text="" />
  <van-goods-action-icon icon="cart-o" @click="jiarugwc" text="" info="9" />
  <van-goods-action-icon icon="shop-o" text="" />
  <van-goods-action-button color="#be99ff" @click="jiarugwc" text="" type="warning" />
  <van-goods-action-button color="#7232dd" @click="submitHandler" block type="primary" text="" />
</van-goods-action>
    </div>
  </briup-fulllayout>
</template>
<script>
import { mapState } from 'vuex'
import {get,post_obj_array} from '../../../http/axios'
export default {
  data(){
    return {
      addressId:0,    // id
      addresses:[],
      orderLines:[]
    }
  },
  created(){
    // 
    this.loadAddress();
    // 
    let orderLine = {
      number:1,
      price:this.$route.query.price,
      productId:this.$route.query.id
    };
    this.orderLines.push(orderLine);
  },
  computed:{
    // info
    ...mapState("user",["info"]),
    // [{id:1,province:'',city:""},{id:1,province:'',city:""}] = [{text:"",value:1}]
    options:function(){
      // addresses
      return this.addresses.map(item=>{
        return {
          text:item.province+" "+item.city+" "+item.area+" "+item.address,
          value:item.id
        }
      })
    }
  },
  methods:{
    // 
    submitHandler(){
      let url = "/order/save";
      let data = {
        customerId:this.info.id,
        addressId: this.addressId,
        orderLines:this.orderLines
      }
      // console.log(data);
      post_obj_array(url,data).then(response =>{
        this.$toast("");
        this.$router.push("/manager/order")
      })
    },
    jiarugwc(){
      let url = "/order/save";
      let data = {
        customerId:this.info.id,
        addressId: this.addressId,
        orderLines:this.orderLines
      }
      // console.log(data);
      post_obj_array(url,data).then(response =>{
        this.$toast("");
        this.$router.push("/manager/product")
      })
    },
    // 
    loadAddress(){
      let id = this.info.id;  
      let url = "/address/findByCustomerId?id="+id;
      get(url).then((response)=>{
        // address
        this.addresses = response.data;
        // iddataaddressId,
        this.addressId = this.addresses[0].id;
      })
    }
  }
}
</script>