<template>
  <div class="order">
    <van-nav-bar title="" />
    <van-tabs @click="tabClickHandler" v-model="active" color="#1659a0">
      <van-tab title=""></van-tab>
      <van-tab title=""></van-tab>
      <van-tab title=""></van-tab> 
      <van-tab title=""></van-tab> 
      <van-tab title=""></van-tab> 
      <van-tab title=""></van-tab> 
      <van-tab title=""></van-tab>
    </van-tabs>
    <!--  -->
    <div>
      <briup-order-item 
        v-for="o in orders" 
        :key="o.id" 
        :data="o">
      </briup-order-item>
    </div>      


  </div>
</template>
<script>
import { mapState } from 'vuex'
import {get} from '../../http/axios'
export default {
  data(){
    return {
      active:0,
      orders:[],
      status:null
    }
  },
  computed:{
    ...mapState("user",["info"])
  },
  created(){
    this.loadOrders();
  },
  methods:{
    // tab
    tabClickHandler(name,title){
      // datastatus 
      this.status = title === "" ? null : title;
      this.loadOrders();
    },
    // 
    loadOrders(){
      let url = "/order/query";
      let params = {
        customerId:this.info.id,
        status:this.status
      }
      get(url,params).then((response)=>{
        this.orders = response.data;
      })
    }
  }
}
</script>
<style scoped>
.order {
  background: #f5f1f1;
}
</style>